// import axios from 'axios';
import { require } from './require';
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

export function userLogin(userInfo) {  // 
  return require({
    url: '/user/login.do',
    method: 'post',
    data: userInfo
  })
}

export function userRegister(userInfo) {  // 
  return require({
    url: '/user/register.do',
    method: 'post',
    data: userInfo
  })
}

export function checkRegisterUsername(username) {  // 
  return require({
    url: '/user/check_valid.do',
    method: 'post',
    data: username
  })
}

export function getUserInfo() {  // 
  return require({
    url: '/user/get_user_info.do',
    method: 'post'
  })
}

export function getQuestion(username) {  //  
  return require({
    url: '/user/forget_get_question.do',
    method: 'post',
    data: username
  })
}

export function checkAnswer(pwdInfo) {  //  token
  return require({
    url: '/user/forget_check_answer.do',
    method: 'post',
    data: pwdInfo
  })
}

export function setNewPwd(pwdInfo) {  //  
  return require({
    url: '/user/forget_reset_password.do',
    method: 'post',
    data: pwdInfo
  })
}

export function logout() {  // 
  return require({
    url: '/user/logout.do',
    method: 'post'
  })
}

export function updateInfo(info) {  // 
  return require({
    url: '/user/update_information.do',
    method: 'post',
    data: info
  })
}

export function updatePwd(pwdInfo) {  // 
  return require({
    url: '/user/reset_password.do',
    method: 'post',
    data: pwdInfo
  })
}


